<template>
  <div class="article-detail-container">
    <el-card class="article-card">
      <div slot="header" class="header">
        <h1>{{ article.title }}</h1>
        <div class="article-meta">
          <span class="author">ä½œè€…: {{ article.authorName || 'æœªçŸ¥' }}</span>
          <span class="time">å‘å¸ƒæ—¶é—´: {{ formatDate(article.createTime) }}</span>
          <span class="views">é˜…è¯»: {{ article.viewCount }}</span>
          <span class="likes">ç‚¹èµ: {{ article.likeCount }}</span>
          <span class="status" :class="getStatusClass(article.status)">
            {{ getStatusText(article.status) }}
          </span>
        </div>
        
        <!-- ç®¡ç†å‘˜å®¡æ ¸æ“ä½œåŒº -->
        <div class="admin-actions" v-if="isAdmin && article.status === 2">
          <el-button type="success" size="small" @click="approveArticle">å®¡æ ¸é€šè¿‡</el-button>
          <el-button type="danger" size="small" @click="rejectArticle">å®¡æ ¸æ‹’ç»</el-button>
        </div>
        
        <!-- ä½œè€…ç¼–è¾‘æ“ä½œåŒº -->
        <div class="author-actions" v-if="isAuthor && (article.status === 2 || article.status === 3)">
          <el-button type="primary" size="small" @click="editArticle">ç¼–è¾‘æ–‡ç« </el-button>
        </div>
      </div>
      
      <div class="article-content" v-html="article.content"></div>
      
      <div class="article-footer">
        <el-button 
          :type="isLiked ? 'success' : 'primary'" 
          :icon="isLiked ? 'el-icon-star-on' : 'el-icon-thumb'" 
          @click="toggleLike"
          :loading="liking">
          {{ isLiked ? 'å·²ç‚¹èµ' : 'ç‚¹èµ' }} {{ article.likeCount }}
        </el-button>
        
        <div class="tags" v-if="article.tags && article.tags.length">
          <el-tag 
            v-for="tag in article.tags" 
            :key="tag.id"
            :type="tag.color || 'info'"
            size="small">
            {{ tag.name }}
          </el-tag>
        </div>
      </div>
    </el-card>

    <!-- è¯„è®ºåŒº -->
    <el-card class="comments-card" v-if="article.id">
      <div slot="header" class="comments-header">
        <div class="comments-title">
          <i class="el-icon-chat-dot-round"></i>
          <span>è¯„è®º ({{ commentCount }})</span>
        </div>
        <div class="comments-info" v-if="commentCount > 0">
          <el-tooltip content="è¯„è®ºè§„åˆ™" placement="top">
            <i class="el-icon-info"></i>
          </el-tooltip>
          <span>æ–‡æ˜è¯„è®ºï¼Œç†æ€§å‘è¨€</span>
        </div>
      </div>
      
      <comment-section 
        :article-id="article.id"
        @comment-count-updated="updateCommentCount"
      ></comment-section>
    </el-card>
  </div>
</template>

<script>
import CommentSection from '../components/CommentSection.vue'
import api from '../api'

export default {
  name: 'ArticleDetail',
  components: {
    CommentSection
  },
  data() {
    return {
      article: {
        id: null,
        title: '',
        content: '',
        authorName: '',
        createTime: '',
        viewCount: 0,
        likeCount: 0,
        commentCount: 0,
        tags: [],
        status: 1, // é»˜è®¤å·²å‘å¸ƒçŠ¶æ€
        userId: null // ä½œè€…ID
      },
      commentCount: 0, // å®æ—¶è¯„è®ºæ•°
      liking: false,
      isAdmin: false, // æ˜¯å¦ä¸ºç®¡ç†å‘˜
      currentUserId: null, // å½“å‰ç™»å½•ç”¨æˆ·ID
      isLiked: false // æ˜¯å¦å·²ç‚¹èµ
    }
  },
  computed: {
    // åˆ¤æ–­å½“å‰ç”¨æˆ·æ˜¯å¦ä¸ºæ–‡ç« ä½œè€…
    isAuthor() {
      return this.currentUserId && this.article.userId && this.currentUserId === this.article.userId;
    }
  },
  created() {
    this.getUserInfo()
    this.getArticleDetail()
  },
  
  watch: {
    // ç›‘å¬æ–‡ç« IDå˜åŒ–ï¼Œé‡æ–°æ£€æŸ¥ç‚¹èµçŠ¶æ€
    'article.id': function(newVal) {
      if (newVal) {
        this.checkLikeStatus()
      }
    }
  },
  methods: {
    // è·å–å½“å‰ç”¨æˆ·ä¿¡æ¯
    async getUserInfo() {
      try {
        // æ£€æŸ¥æ˜¯å¦å·²ç™»å½•
        if (!this.$store.getters.isAuthenticated) {
          console.log('ç”¨æˆ·æœªç™»å½•ï¼Œæ— æ³•è·å–ç”¨æˆ·ä¿¡æ¯')
          return
        }
        
        const response = await api.user.getInfo()
        if (response.code === 200) {
          const userInfo = response.data
          this.currentUserId = userInfo.id
          // æ£€æŸ¥ç”¨æˆ·æ˜¯å¦ä¸ºç®¡ç†å‘˜
          this.isAdmin = userInfo.role === 0
          console.log('è·å–ç”¨æˆ·ä¿¡æ¯æˆåŠŸ:', userInfo)
        }
      } catch (error) {
        console.error('è·å–ç”¨æˆ·ä¿¡æ¯å¤±è´¥:', error)
        // ä»storeä¸­è·å–åŸºæœ¬ç”¨æˆ·ä¿¡æ¯
        const storeUser = this.$store.getters.user
        if (storeUser) {
          this.currentUserId = storeUser.id
          this.isAdmin = storeUser.role === 0
          console.log('ä»storeè·å–ç”¨æˆ·ä¿¡æ¯:', storeUser)
        }
      }
    },
    
    async getArticleDetail() {
      try {
        const articleId = this.$route.params.id
        const response = await api.article.getDetail(articleId)
        if (response.code === 200) {
          this.article = response.data
          // åˆå§‹åŒ–è¯„è®ºè®¡æ•°
          this.commentCount = this.article.commentCount || 0
          
          // æ£€æŸ¥ç‚¹èµçŠ¶æ€
          this.checkLikeStatus()
          
          // å¢åŠ æ–‡ç« æµè§ˆé‡
          this.incrementViewCount()
        } else {
          // å¤„ç†ä¸šåŠ¡é”™è¯¯
          this.$message.error(response.message || 'è·å–æ–‡ç« è¯¦æƒ…å¤±è´¥')
          if (response.message === 'æ‚¨æ— æƒæŸ¥çœ‹è¯¥æ–‡ç« ') {
            setTimeout(() => {
              this.$router.push('/article')
            }, 1500)
          }
        }
      } catch (error) {
        console.error('è·å–æ–‡ç« è¯¦æƒ…å¤±è´¥:', error)
        let errorMsg = 'è·å–æ–‡ç« è¯¦æƒ…å¤±è´¥'
        
        if (error.response) {
          switch (error.response.status) {
            case 403:
              errorMsg = 'æ‚¨æ— æƒæŸ¥çœ‹è¯¥æ–‡ç« '
              break
            case 404:
              errorMsg = 'è¯·æ±‚çš„èµ„æºä¸å­˜åœ¨'
              break
            default:
              errorMsg = `è·å–æ–‡ç« è¯¦æƒ…å¤±è´¥: ${error.message || 'æœªçŸ¥é”™è¯¯'}`
          }
        } else if (error.message) {
          errorMsg = `è·å–æ–‡ç« è¯¦æƒ…å¤±è´¥: ${error.message}`
        }
        
        this.$message.error(errorMsg)
        setTimeout(() => {
          this.$router.push('/article')
        }, 1500)
      }
    },
    
    // å¢åŠ æ–‡ç« æµè§ˆé‡
    async incrementViewCount() {
      try {
        // è¿™é‡Œå¯ä»¥æ·»åŠ å¢åŠ æµè§ˆé‡çš„APIè°ƒç”¨
        // æš‚æ—¶ä¸å®ç°ï¼Œå› ä¸ºåç«¯å¯èƒ½å·²ç»åœ¨è·å–æ–‡ç« è¯¦æƒ…æ—¶è‡ªåŠ¨å¢åŠ äº†æµè§ˆé‡
      } catch (error) {
        console.error('å¢åŠ æµè§ˆé‡å¤±è´¥:', error)
      }
    },
    
    // æ£€æŸ¥å½“å‰ç”¨æˆ·æ˜¯å¦å·²ç‚¹èµè¯¥æ–‡ç« 
    async checkLikeStatus() {
      if (!this.article.id) {
        return
      }
      
      // æ£€æŸ¥æ˜¯å¦æœ‰token
      const token = localStorage.getItem('token')
      if (!token) {
        console.log('ç”¨æˆ·æœªç™»å½•ï¼Œæ— æ³•æ£€æŸ¥ç‚¹èµçŠ¶æ€')
        this.isLiked = false
        return
      }
      
      try {
        const response = await api.article.checkLikeStatus(this.article.id)
        if (response.code === 200) {
          this.isLiked = response.data
          console.log('ç‚¹èµçŠ¶æ€:', this.isLiked ? 'å·²ç‚¹èµ' : 'æœªç‚¹èµ')
        }
      } catch (error) {
        console.error('æ£€æŸ¥ç‚¹èµçŠ¶æ€å¤±è´¥:', error)
        // é”™è¯¯ä¸å½±å“ç”¨æˆ·ä½“éªŒï¼Œåªè®°å½•æ—¥å¿—
        this.isLiked = false
      }
    },
    
    // ç‚¹èµæˆ–å–æ¶ˆç‚¹èµ
    async toggleLike() {
      // æ£€æŸ¥æ˜¯å¦æœ‰token
      const token = localStorage.getItem('token')
      if (!token) {
        this.$message.warning('è¯·å…ˆç™»å½•')
        // ä¿å­˜å½“å‰é¡µé¢URLï¼Œç™»å½•åå¯ä»¥è¿”å›
        localStorage.setItem('redirectUrl', this.$route.fullPath)
        // è·³è½¬åˆ°ç™»å½•é¡µ
        this.$router.push('/login')
        return
      }
      
      try {
        this.liking = true
        
        // ç¡®ä¿tokenå·²è®¾ç½®åˆ°storeä¸­
        if (!this.$store.getters.token) {
          this.$store.commit('SET_TOKEN', token)
        }
        
        // å°è¯•è·å–ç”¨æˆ·ä¿¡æ¯ï¼ˆå¦‚æœè¿˜æ²¡æœ‰ï¼‰
        if (!this.currentUserId) {
          // å°è¯•ä»storeè·å–ç”¨æˆ·ä¿¡æ¯
          const storeUser = this.$store.getters.user
          if (storeUser && storeUser.id) {
            this.currentUserId = storeUser.id
            this.isAdmin = storeUser.role === 0
          } else {
            // å°è¯•ä»APIè·å–ç”¨æˆ·ä¿¡æ¯
            try {
              await this.$store.dispatch('getUserInfo')
              const updatedUser = this.$store.getters.user
              if (updatedUser) {
                this.currentUserId = updatedUser.id
                this.isAdmin = updatedUser.role === 0
              } else {
                throw new Error('æ— æ³•è·å–ç”¨æˆ·ä¿¡æ¯')
              }
            } catch (userError) {
              console.error('è·å–ç”¨æˆ·ä¿¡æ¯å¤±è´¥:', userError)
              this.$message.error('ç™»å½•çŠ¶æ€å¼‚å¸¸ï¼Œè¯·é‡æ–°ç™»å½•')
              this.$store.dispatch('logout')
              localStorage.setItem('redirectUrl', this.$route.fullPath)
              this.$router.push('/login')
              return
            }
          }
        }
        
        // æ‰§è¡Œç‚¹èµæ“ä½œ
        const response = await api.article.like(this.article.id)
        
        if (response.code === 200) {
          // åˆ‡æ¢ç‚¹èµçŠ¶æ€
          this.isLiked = !this.isLiked
          
          // æ›´æ–°ç‚¹èµæ•°
          if (this.isLiked) {
            this.article.likeCount++
            this.$message.success('ç‚¹èµæˆåŠŸ')
          } else {
            if (this.article.likeCount > 0) {
              this.article.likeCount--
            }
            this.$message.success('å·²å–æ¶ˆç‚¹èµ')
          }
        }
      } catch (error) {
        console.error('ç‚¹èµæ“ä½œå¤±è´¥:', error)
        if (error.response) {
          if (error.response.status === 401 || error.response.status === 403) {
            this.$message.error('ç™»å½•å·²è¿‡æœŸï¼Œè¯·é‡æ–°ç™»å½•')
            // æ¸…é™¤tokenå¹¶é‡å®šå‘åˆ°ç™»å½•é¡µ
            this.$store.dispatch('logout')
            localStorage.setItem('redirectUrl', this.$route.fullPath)
            this.$router.push('/login')
          } else {
            this.$message.error('æ“ä½œå¤±è´¥: ' + (error.message || 'æœªçŸ¥é”™è¯¯'))
          }
        } else {
          this.$message.error('æ“ä½œå¤±è´¥: ' + (error.message || 'ç½‘ç»œé”™è¯¯'))
        }
      } finally {
        this.liking = false
      }
    },
    
    // è·å–æ–‡ç« çŠ¶æ€æ–‡æœ¬
    getStatusText(status) {
      switch (status) {
        case 0: return 'è‰ç¨¿';
        case 1: return 'å·²å‘å¸ƒ';
        case 2: return 'å¾…å®¡æ ¸';
        case 3: return 'å®¡æ ¸ä¸é€šè¿‡';
        default: return 'æœªçŸ¥çŠ¶æ€';
      }
    },
    
    // è·å–æ–‡ç« çŠ¶æ€æ ·å¼ç±»
    getStatusClass(status) {
      switch (status) {
        case 0: return 'status-draft';
        case 1: return 'status-published';
        case 2: return 'status-pending';
        case 3: return 'status-rejected';
        default: return '';
      }
    },
    
    // å®¡æ ¸é€šè¿‡æ–‡ç« 
    async approveArticle() {
      try {
        const response = await api.article.approve(this.article.id)
        if (response.code === 200) {
          this.$message.success('å®¡æ ¸é€šè¿‡æˆåŠŸ')
          this.article.status = 1 // æ›´æ–°çŠ¶æ€ä¸ºå·²å‘å¸ƒ
        }
      } catch (error) {
        this.$message.error('å®¡æ ¸æ“ä½œå¤±è´¥: ' + (error.message || 'æœªçŸ¥é”™è¯¯'))
      }
    },
    
    // å®¡æ ¸æ‹’ç»æ–‡ç« 
    async rejectArticle() {
      try {
        const response = await api.article.reject(this.article.id)
        if (response.code === 200) {
          this.$message.success('å®¡æ ¸æ‹’ç»æˆåŠŸ')
          this.article.status = 3 // æ›´æ–°çŠ¶æ€ä¸ºå®¡æ ¸ä¸é€šè¿‡
        }
      } catch (error) {
        this.$message.error('å®¡æ ¸æ“ä½œå¤±è´¥: ' + (error.message || 'æœªçŸ¥é”™è¯¯'))
      }
    },
    
    // ç¼–è¾‘æ–‡ç« 
    editArticle() {
      this.$router.push(`/write?id=${this.article.id}`)
    },
    
    // æ›´æ–°è¯„è®ºè®¡æ•°
    updateCommentCount(count) {
      this.commentCount = count
      // åŒæ—¶æ›´æ–°æ–‡ç« å¯¹è±¡ä¸­çš„è¯„è®ºæ•°
      this.article.commentCount = count
    },
    
    // æ ¼å¼åŒ–æ—¥æœŸ
    formatDate(dateString) {
      if (!dateString) return '';
      const date = new Date(dateString);
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`;
    }
  }
}
</script>

<style scoped>
.article-detail-container {
  padding: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.article-card {
  margin-bottom: 20px;
}

.header h1 {
  margin: 0 0 10px 0;
  color: #333;
}

.article-meta {
  display: flex;
  gap: 20px;
  color: #666;
  font-size: 14px;
