}

.status-pending {
  background-color: #E6A23C;
  color: white;
}

.status-rejected {
  background-color: #F56C6C;
  color: white;
}

.admin-actions, .author-actions {
  margin-top: 10px;
  display: flex;
  gap: 10px;
}

.article-content {
  line-height: 1.8;
  font-size: 16px;
}

.article-content >>> img {
  max-width: 100%;
