}

.comments-card >>> .el-card__header {
  background: linear-gradient(135deg, #f6f9fc 0%, #eef1f5 100%);
  padding: 15px 20px;
}

.comments-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.comments-title {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 16px;
  font-weight: 600;
  color: #333;
}

.comments-title i {
  color: #409EFF;
