}

.comments-info {
  display: flex;
  align-items: center;
  gap: 5px;
  color: #909399;
  font-size: 13px;
}

.comments-info i {
  color: #E6A23C;
  cursor: pointer;
}

.comments-card >>> .el-card__body {
  padding: 0;
}
</style>